import Vue from 'vue'
import App from './App.vue'
import axios from 'axios'
import VuePaginate from 'vue-paginate'
import router from './router'

Vue.prototype.$http = axios

Vue.config.productionTip = false
Vue.use(VuePaginate)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
